
import { AjaxHttp, AjaxImageHttp } from '../../../js/ajaxHttp'
import { isArrayType, isPostNullValue, getYMDHMFormateTimestamp, getYMDFormateTimestamp, compressImage, createBlob } from "../../../js/utility.js"
import { mapState } from 'vuex'
import event from '../../../js/Event.js'
import Selector from 'vux/src/components/selector/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

import { getGeneralExamineInfo, getLeaveExamineInfo, getGeneralApplyInfo, getTravelApplyInfo, getTravelExamineInfo, getLoanExamineInfo } from '../data/examineData.js'


export default {
name: 'examinehandle',
components: {
XTextarea,
Selector,
XInput,
Group,
Datetime,
Checker,
CheckerItem,
Previewer
},
computed: {
...mapState([
'userInfo',
'examineHandle',
'detail', //详情
]),
showImageUploader() {
if ((this.$route.params.type === '3' || this.$route.params.type === '4') && this.$route.params.action === 'add') {
return true;
}
return false;
}
},
beforeRouteLeave(to, from, next) {

if (to.name === 'selectlist') {
event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));
if (this.showImageUploader) {
var item = this.examineHandle[this.examineHandle.length - 1];
if (item.form_type === 'file') {
item.files = this.files;
} else {
this.examineHandle.push({ form_type: 'file', files: this.files });
}
}
} else if (to.name === 'examinemain' || to.name === 'examineview') { //离开页面返回上层，需要重置数据 //添加页面返回普通列表  编辑页面
this.$store.commit('updateExamineHandle', []);
}
next();
},
beforeRouteEnter(to, from, next) {
if (from.name === 'selectlist') {
event.$off("selectedRelateItem");
}
next();
},
data() {
return {
travelApplyOption: [
{ 'key': '飞机', 'value': '飞机' },
{ 'key': '汽车', 'value': '汽车' },
{ 'key': '火车', 'value': '火车' },
{ 'key': '高铁', 'value': '高铁' },
{ 'key': '轮船', 'value': '轮船' },
{ 'key': '其他', 'value': '其他' }
],
files: [],
uploadfile: false, //如果为true 不能保存
}
},
beforeDestroy() {
event.$off("saveDone");
},
mounted() {
if (this.examineHandle.length === 0) {
AjaxHttp('POST', 'm=examine&a=examineStatus', {
type: this.$route.params.type
}, this, function(res, wself, success) {
if (success) {
console.log(res);
wself.handleAddEditInfo(res);
}
});
} else {
if (this.showImageUploader) {
var item = this.examineHandle[this.examineHandle.length - 1];
if (item.form_type === 'file') {
this.files = item.files;
}
}
}

event.$on('saveDone', function() {
this.saveInfos();
}.bind(this));
},
methods: {
handleAddEditInfo(flowData) {
if (this.$route.params.type === '1') {
if (this.$route.params.action === 'add') {
this.$store.commit('updateExamineHandle', getGeneralExamineInfo('add', {}, this.userInfo, flowData));
} else if (this.$route.params.action === 'edit' && this.detail.type === 'examineview' && this.detail.examine_detail.examine_id === this.$route.params.id) {
this.$store.commit('updateExamineHandle', getGeneralExamineInfo('edit', this.detail.examine_detail, this.userInfo, flowData));
}
} else if (this.$route.params.type === '2') {
if (this.$route.params.action === 'add') {
this.$store.commit('updateExamineHandle', getLeaveExamineInfo('add', {}, this.userInfo, flowData));
} else if (this.$route.params.action === 'edit' && this.detail.type === 'examineview' && this.detail.examine_detail.examine_id === this.$route.params.id) {
this.$store.commit('updateExamineHandle', getLeaveExamineInfo('edit', this.detail.examine_detail, this.userInfo, flowData));
}
} else if (this.$route.params.type === '3') {
if (this.$route.params.action === 'add') {
this.$store.commit('updateExamineHandle', getGeneralApplyInfo('add', {}, this.userInfo, flowData));
} else if (this.$route.params.action === 'edit' && this.detail.type === 'examineview' && this.detail.examine_detail.examine_id === this.$route.params.id) {
this.$store.commit('updateExamineHandle', getGeneralApplyInfo('edit', this.detail.examine_detail, this.userInfo, flowData));
}
} else if (this.$route.params.type === '4') {
if (this.$route.params.action === 'add') {
this.$store.commit('updateExamineHandle', getTravelApplyInfo('add', {}, this.userInfo, flowData));
} else if (this.$route.params.action === 'edit' && this.detail.type === 'examineview' && this.detail.examine_detail.examine_id === this.$route.params.id) {
this.$store.commit('updateExamineHandle', getTravelApplyInfo('edit', this.detail.examine_detail, this.userInfo, flowData));
}
} else if (this.$route.params.type === '5') {
if (this.$route.params.action === 'add') {
this.$store.commit('updateExamineHandle', getTravelExamineInfo('add', {}, this.userInfo, flowData));
} else if (this.$route.params.action === 'edit' && this.detail.type === 'examineview' && this.detail.examine_detail.examine_id === this.$route.params.id) {
this.$store.commit('updateExamineHandle', getTravelExamineInfo('edit', this.detail.examine_detail, this.userInfo, flowData));
}
} else if (this.$route.params.type === '6') {
if (this.$route.params.action === 'add') {
this.$store.commit('updateExamineHandle', getLoanExamineInfo('add', {}, this.userInfo, flowData));
} else if (this.$route.params.action === 'edit' && this.detail.type === 'examineview' && this.detail.examine_detail.examine_id === this.$route.params.id) {
this.$store.commit('updateExamineHandle', getLoanExamineInfo('edit', this.detail.examine_detail, this.userInfo, flowData));
}
}
},
saveInfos() {
if (this.uploadfile) { return; }
var add = true;
var postParams = {};
for (var i = 0; i < this.examineHandle.length; i++) {
var item = this.examineHandle[i];
var fieldValue = '';
if (item.form_type === 'text' || item.form_type === 'number' || item.form_type === 'floatnumber' || item.form_type === 'email' || item.form_type === 'mobile' || item.form_type === 'textarea') {
if (item.disabled) {
fieldValue = item.fieldvalue;
} else {
fieldValue = item.content;
}
} else {
fieldValue = item.fieldvalue;
}

if (item.is_null === '1' && isPostNullValue(fieldValue)) {
this.$vux.toast.text(item.name + '是必填项', 'top');
add = false;
break;
}
if (item.form_type === 'datetime' && !isPostNullValue(fieldValue)) {
if (item.data === 'YYYY-MM-DD HH:mm') {
fieldValue = getYMDHMFormateTimestamp(fieldValue);
} else if (item.data === 'YYYY-MM-DD') {
fieldValue = getYMDFormateTimestamp(fieldValue);
}
}

if (item.form_type === 'generalItem') {
var temps = [];
for (var j = 0; j < item.field.length; j++) {
var aitem = item.field[j];
if (isPostNullValue(aitem.start_address)) {
var title = '事项' + (j + 1);
this.$vux.toast.text(title + '的报销项是必填项', 'top');
add = false;
break;
} else if (isPostNullValue(aitem.money)) {
var title = '事项' + (j + 1);
this.$vux.toast.text(title + '的金额(元)是必填项', 'top');
add = false;
break;
} else {
if (aitem.id) {
temps.push({ start_address: aitem.start_address, money: aitem.money, description: aitem.description, id: aitem.id });
} else {
temps.push({ start_address: aitem.start_address, money: aitem.money, description: aitem.description });
}

}
}
postParams['travel'] = temps;
} else if (item.form_type === 'travelItem') {
var temps = [];
for (var j = 0; j < item.field.length; j++) {
var aitem = item.field[j];
if (isPostNullValue(aitem.start_address)) {
var title = '事项' + (j + 1);
this.$vux.toast.text(title + '的出发地是必填项', 'top');
add = false;
break;
} else if (isPostNullValue(aitem.end_address)) {
var title = '事项' + (j + 1);
this.$vux.toast.text(title + '的目的地是必填项', 'top');
add = false;
break;
} else if (isPostNullValue(aitem.money)) {
var title = '事项' + (j + 1);
this.$vux.toast.text(title + '的金额(元)是必填项', 'top');
add = false;
break;
} else if (isPostNullValue(aitem.start_time)) {
var title = '事项' + (j + 1);
this.$vux.toast.text(title + '的出发时间是必填项', 'top');
add = false;
break;
} else if (isPostNullValue(aitem.end_time)) {
var title = '事项' + (j + 1);
this.$vux.toast.text(title + '的到达时间是必填项', 'top');
add = false;
break;
} else {
if (aitem.id) {
temps.push({ start_address: aitem.start_address, end_address: aitem.end_address, vehicle: aitem.vehicle, money: aitem.money, start_time: getYMDFormateTimestamp(aitem.start_time), end_time: getYMDFormateTimestamp(aitem.end_time), duration: aitem.duration, description: aitem.description, id: aitem.id });
} else {
temps.push({ start_address: aitem.start_address, end_address: aitem.end_address, vehicle: aitem.vehicle, money: aitem.money, start_time: getYMDFormateTimestamp(aitem.start_time), end_time: getYMDFormateTimestamp(aitem.end_time), duration: aitem.duration, description: aitem.description });
}

}
}
postParams['travel'] = temps;
} else { //其他
postParams[item.field] = fieldValue;
}
}

if (this.showImageUploader) {
var postfiles = [];
for (var i = 0; i < this.files.length; i++) {
var file = this.files[i];
postfiles.push(file.file_id);
}
postParams['file'] = postfiles;
}
if (add) {
this.requestHandle(postParams);
}
},
requestHandle(postParams) {
postParams['type'] = this.$route.params.type;
var url = '';
if (this.$route.params.action === 'add') {
url = 'm=examine&a=add';
} else if (this.$route.params.action === 'edit') {
postParams['id'] = this.$route.params.id;
url = 'm=examine&a=edit';
}
AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.$store.commit('updateExamineHandle', []);
if (wself.$route.params.action === 'edit') { //编辑成功刷新上个页面
wself.detail.refreshList = true;
wself.$store.commit('updateDetail', wself.detail.refreshList);
}
wself.$router.go(-1);
}
});
},
onRelateClick(item, row) {
if (!item.disabled) {
if (item.form_type === 'examine') {
this.$router.push({
name: 'selectlist',
params: {
id: '-1',
type: 'examine',
selecttype: '3',
section: '-1',
row: row.toString(),
}
});
}

}
},
addGeneralApplyItem(item, type) {
if (type === 'general') {
item.field.push({ start_address: '', money: '', description: '' });
} else if (type === 'travel') {
item.field.push({ start_address: '', end_address: '', vehicle: '', money: '', start_time: '', end_time: '', duration: '', description: '' });
}

},
deleteGeneralApplyItem(item, aindex) {
item.field.splice(aindex, 1);
},
handleSelectedInfo(data) {
if (data.type === '3') { //审批人选择
var item = this.examineHandle[parseInt(data.row)];
item.content = data.item.user_name;
item.fieldvalue = data.item.role_id;
this.$store.commit('updateExamineHandle', this.examineHandle);
}
},
uploadimg(e) {
var src, url = window.URL || window.webkitURL || window.mozURL,
files = e.target.files;
for (var i = 0, len = files.length; i < len; ++i) {
var file = files[i];
var self = this;
compressImage(file, 0.5, (code) => {
var formdata = new FormData();
formdata.append('file', createBlob(code), file.name);
self.uploadfile = true; 
AjaxImageHttp('POST', 'm=file&a=add', formdata, self, function(res, wself, success) {
if (success) {

wself.files.push({ 'name': file.name, 'size': '10px', 'file_path': code, 'file_id': res.data, 'file_type': 'jpg', 'local': true });
wself.uploadfile = false; 
}
});
});
}
},









previewerImg(images, imgindex) {
var previewerlist = [];
for (var i = 0; i < images.length; i++) {
var item = images[i];
previewerlist.push({
src: item.file_path
});
}
event.$emit('previewerAction', { list: previewerlist, index: imgindex });
},
deleteFile(item, index) {
this.files.splice(index, 1);
}
}
}




