import { customItem, getYMDHMFormateTime, getYMDFormateTime, isArrayType } from '../../../js/utility.js'
export function getGeneralExamineInfo(action, data, userInfo, flowData) { //action add edit    flowData 0 自选 1固定流程 
  var tempList = [];
  if (action === 'add') {
    tempList.push(customItem('申请人', 'owner_name', "user", "1", userInfo.role_id, userInfo.name, "", true));
  } else if (action === 'edit') {
    tempList.push(customItem('申请人', 'owner_name', "user", "1", data.owner_role_id, data.create_name, "", true));
  }
  tempList.push(customItem('申请事由', 'content', "textarea", "1", data.content, data.content));
  if (flowData.option === '1') { //固定流程
    if (action === 'add') {
      tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", flowData.examine_role.role_id, flowData.examine_role.full_name, '', true));
    } else if (action === 'edit') {
      tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", data.examine_role_id, data.examine_name, '', true));
    }
  } else {
    tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", data.examine_role_id, data.examine_name));
  }
  return tempList;
}


export function getLeaveExamineInfo(action, data, userInfo, flowData) { //action add edit
  var tempList = [];
  if (action === 'add') {
    tempList.push(customItem('请假人', 'owner_name', "user", "1", userInfo.role_id, userInfo.name, "", true));
  } else if (action === 'edit') {
    tempList.push(customItem('请假人', 'owner_name', "user", "1", data.owner_role_id, data.create_name, "", true));
  }

  tempList.push(customItem('请假事项', 'content', "select", "0", data.content, data.content, [{ 'key': '事假', 'value': '事假' }, { 'key': '病假', 'value': '病假' }, { 'key': '调休', 'value': '调休' }, { 'key': '年休假', 'value': '年休假' }, { 'key': '婚假', 'value': '婚假' }, { 'key': '丧假', 'value': '丧假' }, { 'key': '生育假', 'value': '生育假' }, { 'key': '外勤', 'value': '外勤' }, { 'key': '其他', 'value': '其他' }]));
  tempList.push(customItem('请假事由', 'description', "textarea", "1", data.description, data.description));
  tempList.push(customItem('开始时间', 'start_time', "datetime", "1", getYMDHMFormateTime(data.start_time), getYMDHMFormateTime(data.start_time), 'YYYY-MM-DD HH:mm'));
  tempList.push(customItem('结束时间', 'end_time', "datetime", "1", getYMDHMFormateTime(data.end_time), getYMDHMFormateTime(data.end_time), 'YYYY-MM-DD HH:mm'));
  tempList.push(customItem('请假天数', 'duration', "number", "1", data.duration, data.duration));
  if (flowData.option === '1') { //固定流程
    if (action === 'add') {
      tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", flowData.examine_role.role_id, flowData.examine_role.full_name, '', true));
    } else if (action === 'edit') {
      tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", data.examine_role_id, data.examine_name, '', true));
    }
  } else {
    tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", data.examine_role_id, data.examine_name));
  }
  return tempList;
}

export function getGeneralApplyInfo(action, data, userInfo, flowData) { //action add edit
  var tempList = [];
  if (action === 'add') {
    tempList.push(customItem('申请人', 'owner_name', "user", "1", userInfo.role_id, userInfo.name, "", true));
  } else if (action === 'edit') {
    tempList.push(customItem('申请人', 'owner_name', "user", "1", data.owner_role_id, data.create_name, "", true));
  }

  tempList.push(customItem('申请事由', 'content', "textarea", "1", data.content, data.content));
  if (flowData.option === '1') { //固定流程
    if (action === 'add') {
      tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", flowData.examine_role.role_id, flowData.examine_role.full_name, '', true));
    } else if (action === 'edit') {
      tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", data.examine_role_id, data.examine_name, '', true));
    }
  } else {
    tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", data.examine_role_id, data.examine_name));
  }
  tempList.push(customItem('总金额(元)', 'budget', "number", "1", data.budget, data.budget));

  if (action === 'add') {
    tempList.push(customItem('事项', [{ start_address: '', money: '', description: '' }], 'generalItem', ));
  } else if (action === 'edit') {
    var listArray = [];
    if (isArrayType(data.travel)&&data.travel.length > 0) {
      for (var i = 0; i < data.travel.length; i++) {
        var list = {};
        var item = data.travel[i];
        list.start_address = item.start_address;
        list.money = item.money;
        list.description = item.description;
        list.id = item.id;
        listArray.push(list);
      }
    } else {
      listArray.push({ start_address: '', money: '', description: '' });
    }
    tempList.push(customItem('事项', listArray, 'generalItem', ));

  }
  return tempList;
}

export function getTravelApplyInfo(action, data, userInfo, flowData) { //action add edit
  var tempList = [];
  if (action === 'add') {
    tempList.push(customItem('申请人', 'owner_name', "user", "1", userInfo.role_id, userInfo.name, "", true));
  } else if (action === 'edit') {
    tempList.push(customItem('申请人', 'owner_name', "user", "1", data.owner_role_id, data.create_name, "", true));
  }

  tempList.push(customItem('申请事由', 'content', "textarea", "1", data.content, data.content));
  if (flowData.option === '1') { //固定流程
    if (action === 'add') {
      tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", flowData.examine_role.role_id, flowData.examine_role.full_name, '', true));
    } else if (action === 'edit') {
      tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", data.examine_role_id, data.examine_name, '', true));
    }
  } else {
    tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", data.examine_role_id, data.examine_name));
  }
  tempList.push(customItem('总金额(元)', 'budget', "number", "1", data.budget, data.budget));

  if (action === 'add') {
    tempList.push(customItem('事项', [{ start_address: '', end_address: '', vehicle: '', money: '', start_time: '', end_time: '', duration: '', description: '' }], 'travelItem', ));
  } else if (action === 'edit') {
    var listArray = [];
    if (isArrayType(data.travel)&&data.travel.length > 0) {
      for (var i = 0; i < data.travel.length; i++) {
        var list = {};
        var item = data.travel[i];
        list.start_address = item.start_address;
        list.end_address = item.end_address;
        list.vehicle = item.vehicle;
        list.money = item.money;
        list.start_time = getYMDFormateTime(item.start_time);
        list.end_time = getYMDFormateTime(item.end_time);
        list.duration = item.duration;
        list.description = item.description;
        list.id = item.id;
        listArray.push(list);
      }
    } else {
      listArray.push({ start_address: '', end_address: '', vehicle: '', money: '', start_time: '', end_time: '', duration: '', description: '' });
    }
    tempList.push(customItem('事项', listArray, 'travelItem', ));
  }

  return tempList;
}

export function getTravelExamineInfo(action, data, userInfo, flowData) { //action add edit
  var tempList = [];
  if (action === 'add') {
    tempList.push(customItem('出差人', 'owner_name', "user", "1", userInfo.role_id, userInfo.name, "", true));
  } else if (action === 'edit') {
    tempList.push(customItem('出差人', 'owner_name', "user", "1", data.owner_role_id, data.create_name, "", true));
  }

  tempList.push(customItem('出差事由', 'content', "textarea", "1", data.content, data.content));
  if (flowData.option === '1') { //固定流程
    if (action === 'add') {
      tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", flowData.examine_role.role_id, flowData.examine_role.full_name, '', true));
    } else if (action === 'edit') {
      tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", data.examine_role_id, data.examine_name, '', true));
    }
  } else {
    tempList.push(customItem('审批人', 'examine_role_id', "examine", "1", data.examine_role_id, data.examine_name));
  }
  tempList.push(customItem('预算(元)', 'budget', "number", "1", data.budget, data.budget));
  tempList.push(customItem('出发地', 'start_address', "text", "1", data.start_address, data.start_address));
  tempList.push(customItem('目的地', 'end_address', "text", "1", data.end_address, data.end_address));
  tempList.push(customItem('出发时间', 'start_time', "datetime", "1", getYMDFormateTime(data.start_time), getYMDFormateTime(data.start_time), 'YYYY-MM-DD'));
  tempList.push(customItem('返回时间', 'end_time', "datetime", "1", getYMDFormateTime(data.end_time), getYMDFormateTime(data.end_time), 'YYYY-MM-DD'));
  tempList.push(customItem('交通工具', 'vehicle', "select", "0", data.vehicle, data.vehicle, [
    { 'key': '飞机', 'value': '飞机' },
    { 'key': '汽车', 'value': '汽车' },
    { 'key': '火车', 'value': '火车' },
    { 'key': '高铁', 'value': '高铁' },
    { 'key': '轮船', 'value': '轮船' },
    { 'key': '其他', 'value': '其他' }
  ]));
  tempList.push(customItem('住宿(天)', 'duration', "number", "0", data.duration, data.duration));
  tempList.push(customItem('备注', 'description', "textarea", "1", data.description, data.description));
  return tempList;
}

